.about{
    display:flex;
    /* flex-direction:column; */
    align-items:center;
    justify-content: center;
    background-color: rgb(36, 36, 36);
    padding: 70px 0;
    margin: 0 auto;
}

.about__header-container{
    display:flex;
    align-items: center;
    justify-content: center;
    padding-right: 20px;
    height:100px;
}

.about__header{
    text-align: center;
    color:white;
    font-family: 'Montserrat';
    font-size: 25px;
}

.about__description{
    margin:0;
    font-size: 23px;
    color:white;
    width:60%;
    padding-left:10px;
    border-left:1px solid white;
    max-width:900px
}