@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Parisienne&family=Roboto:wght@300&display=swap');

.cards{
    display:flex;
    margin:0;
    padding:40px 0;
    flex-direction: column;
    align-items: center;
    background-color:#0d0d0d;
    border-style:solid;
    border-right:none;
    border-left:none;
    border-width:1px;
    border-image: linear-gradient(to right, #7a6844, #e6e4a7, #7a6844) 1 8%;
}

.cards__header{
    color:white;
    font-family: 'Montserrat';
}

.cards__container{
    display:flex;
    padding: 40px 0;
    gap:40px;
}

.cards__card{
    height:580px;
    margin:0;
    position:relative;
    display:flex;
    align-items:center;
    flex-direction: column;
    list-style-type: none;
    width:350px;
    padding:20px 0;
    background-color: rgb(71, 71, 71);
    border-radius: 20px;
    transition: height linear 0.35s, margin linear 0.35s;
}

.cards__card-image{
    border-radius:10px;
    width:80%;
}

.cards__logo{
    width:160px;
}

.material-symbols-outlined {
  position:absolute;

  margin:0;
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 100;
  background: linear-gradient(90deg, #7a6844, #e6e4a7, #7a6844);
  font-size: 180px;
  width:180px;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: font-variation-settings ease-in-out .2s;
  text-align: center;
}

.cards__card:hover .material-symbols-outlined{
    animation:star cubic-bezier(1, 0, 0, 1) 1 1s;
}

.material-symbols-outlined:last-child {
    margin-top:10px;
    content:'star';
    font-variation-settings:
    'FILL' 1,
    'wght' 400,
    'GRAD' 0,
    'opsz' 100;
    background: black;
    font-size: 0px;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.cards__card:hover .material-symbols-outlined:last-child{
    animation: black-fill 1s cubic-bezier(1, 0.1, 0.15, 1) 0.35s 1;
}

.cards__card-title-container{
    display:flex;
    align-items: center;
    width:80%;
    height:50px;
    justify-content: center;
    margin:10px 0 0;
    padding-bottom:10px;
    border-bottom: 1px solid white;
}

.cards__icon-container{
    position:relative;
    width:300px;
    height:300px;
    background-color:black;
    border-radius: 10px;
    display:flex;
    justify-content: center;
    align-items:center;
}

.cards__card-title{
    text-align: center;
    color:white;
    font-size: 20px;
    font-family: 'Montserrat';
    width:100%;
    margin:0;
}

.cards__card-description-list{
    padding:10px 0 0;
    width:80%;
    display:flex;
    flex-direction:column;
    align-items: center;
    gap:10px;
}

.cards__card-description{
    text-align: left;
    font-size: 16px;
    color:white;
    font-family: 'Roboto';
    width:80%;
    list-style-type:disc
}

@keyframes star {
    0%{
        font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 100;
    }
    100%{
        font-variation-settings:
  'FILL' 1,
  'wght' 400,
  'GRAD' 0,
  'opsz' 100;
    }
}
@keyframes black-fill {
    0%{
        font-size: 0px;

    }
    100%{
        font-size: 100px;
    }
}